"use strict";












  

exports.replace = function (s1) {
  return function (s2) {
    return function (s3) {
      return s3.replace(s1, s2);
    };
  };
};







  

exports.split = function (sep) {
  return function (s) {
    return s.split(sep);
  };
};











  

exports.joinWith = function (s) {
  return function (xs) {
    return xs.join(s);
  };
};
